import React, { Component } from 'react';

import 'reset-css';
import './App.scss';

import Stats from './components/Stats/Stats';
import TodoList from './components/TodoList/TodoList';
import TodoCreator from './components/TodoCreator/TodoCreator';

class App extends Component {
  state = {
    todos: [
      { id: 'id-1', text: '–≠—Ç–æ –≤–∞–∂–Ω–∞—è Todo', type: 'important', completed: false },
      { id: 'id-2', text: '–í–∞–∂–Ω—ã–µ Todo –≤—ã–¥–µ–ª–µ–Ω—ã –∫—Ä–∞—Å–Ω—ã–º —Ü–≤–µ—Ç–æ–º', type: 'important', completed: false },
      { id: 'id-3', text: '–ê —ç—Ç–æ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–∞—è Todo', type: 'standart', completed: false },
      {
        id: 'id-4',
        text:
          '–ß—Ç–æ-–±—ã –¥–æ–±–∞–≤–∏—Ç—å —Å–≤–æ—é Todo –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –≤ –≥—Ä–∞—Ñ–µ –Ω–∏–∂–µ –≤–≤–µ—Å—Ç–∏ —Ç–µ–∫—Å—Ç –∏ –Ω–∞–∂–∞—Ç—å –Ω–∞ –∫–Ω–æ–ø–∫—É –î–æ–±–∞–≤–∏—Ç—å, –ø–æ—Å–ª–µ —á–µ–≥–æ –Ω–æ–≤–∞—è Todo –ø–æ—è–≤–∏—Ç—Å—è –ø–µ—Ä–≤–æ–π –≤ —Å–ø–∏—Å–∫–µ',
        type: 'standart',
        completed: false,
      },
      {
        id: 'id-5',
        text:
          'Todo –º–æ–∂–Ω–æ –¥–≤–∏–≥–∞—Ç—å –≤—ã—à–µ –∏ –Ω–∏–∂–µ –ø—Ä–∏ –ø–æ–º–æ—â–∏ —Å—Ç—Ä–µ–ª–æ—á–µ–∫, —á—Ç–æ –ø–æ–∑–≤–æ–ª—è–µ—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é —Ö—Ä–∞–Ω–∏—Ç—å Todo –∫–∞–∫ –µ–º—É –±–æ–ª—å—à–µ –Ω—Ä–∞–≤–∏—Ç—Å—è (–Ω–∞–ø—Ä–∏–º–µ—Ä –≤–∞–∂–Ω—ã–µ –ø–æ–¥–Ω—è—Ç—å –ø–æ–≤—ã—à–µ, –∞ –≤—ã–ø–æ–ª–Ω–µ–Ω–Ω—ã–µ –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –≤–Ω–∏–∑)',
        type: 'standart',
        completed: false,
      },
      {
        id: 'id-6',
        text: '–ö–∞—Ä–∞–Ω–¥–∞—à–∏–∫ –ø–æ–∑–∂–µ –±—É–¥–µ—Ç –¥–∞–≤–∞—Ç—å –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å Todo',
        type: 'standart',
        completed: false,
      },
      {
        id: 'id-7',
        text: '–ö—Ä–µ—Å—Ç–∏–∫ –ø–æ–ª–Ω–æ—Å—Ç—å—é —É–¥–∞–ª—è–µ—Ç Todo –∏–∑ —Å–ø–∏—Å–∫–∞',
        type: 'standart',
        completed: false,
      },
      {
        id: 'id-8',
        text: '–ê –≤–æ—Ç —Ç–∞–∫ –≤–æ—Ç –≤—ã–≥–ª—è–¥–∏—Ç –≤—ã–ø–æ–ª–Ω–µ–Ω–Ω–∞—è Todo',
        type: 'standart',
        completed: true,
      },
      {
        id: 'id-9',
        text: '–ß–µ–º –±–æ–ª—å—à–µ –≤—ã–ø–æ–ª–Ω–µ–Ω–Ω—ã—Ö Todo —Ç–µ–º –ª—É—á—à–µ ‚úÖ',
        type: 'standart',
        completed: true,
      },
      {
        id: 'id-10',
        text: '–¢–µ–ø–µ—Ä—å –º–æ–∂–Ω–æ —É–¥–∞–ª—è—Ç—å –≤—Å–µ —ç—Ç–∏ Todo –∏ –¥–æ–±–∞–≤–ª—è—Ç—å —Å–≤–æ–∏ üòä',
        type: 'standart',
        completed: true,
      },
    ],
  };

  componentDidMount() {
    const todos = JSON.parse(localStorage.getItem('todos'));
    todos !== null && this.setState({ todos: todos });
  }

  componentDidUpdate(prevProps, prevState) {
    prevState.todos !== this.state.todos && localStorage.setItem('todos', JSON.stringify(this.state.todos));
    // localStorage.clear();
  }

  handleFormSubmit = todo => this.setState({ todos: [todo, ...this.state.todos] });

  upTodo = index => {
    if (index !== 0) {
      const newTodos = this.state.todos;
      const clickedTodo = newTodos[index];
      newTodos.splice(index, 1);
      newTodos.splice(index - 1, 0, clickedTodo);
      console.log(newTodos);

      this.setState({ todos: newTodos });
      localStorage.setItem('todos', JSON.stringify(newTodos));
    }
  };

  downTodo = index => {
    if (index !== this.state.todos.length - 1) {
      const newTodos = this.state.todos;
      const clickedTodo = newTodos[index];
      newTodos.splice(index, 1);
      newTodos.splice(index + 1, 0, clickedTodo);
      console.log(newTodos);

      this.setState({ todos: newTodos });
      localStorage.setItem('todos', JSON.stringify(newTodos));
    }
  };

  onTodoDelete = id => this.setState({ todos: this.state.todos.filter(todo => todo.id !== id) });

  onTodoClick = id => {
    this.setState(prevState => ({
      todos: prevState.todos.map(todo => (todo.id === id ? { ...todo, completed: !todo.completed } : todo)),
    }));
  };

  render() {
    return (
      <>
        <Stats todos={this.state.todos} />
        <TodoList
          todos={this.state.todos}
          upTodo={this.upTodo}
          downTodo={this.downTodo}
          onTodoDelete={this.onTodoDelete}
          onTodoClick={this.onTodoClick}
        />
        <TodoCreator todos={this.state.todos} handleFormSubmit={this.handleFormSubmit} />
      </>
    );
  }
}

export default App;
