import React, { Component } from 'react';
import { v4 as uuidv4 } from 'uuid';

import './TodoCreator.scss';

class TodoCreator extends Component {
  static defaultProps = {
    text: '',
    type: 'standart',
  };

  state = {
    text: this.props.text,
    type: this.props.type,
    placeholders: [
      '–°—Ö–æ–¥–∏—Ç—å –Ω–∞ —Ñ—É—Ç–±–æ–ª',
      '–ö—É–ø–∏—Ç—å –ø—Ä–æ–¥—É–∫—Ç—ã',
      '–ù–∞–≤–µ—Å—Ç–∏—Ç—å —Ä–æ–¥—Å—Ç–≤–µ–Ω–Ω–∏–∫–æ–≤',
      '–ö—É–ø–∏—Ç—å –ø–æ–¥–∞—Ä–æ–∫',
      '–ü–æ–º—ã—Ç—å –ø–æ—Å—É–¥—É',
      '–ü—Ä–∏–±—Ä–∞—Ç—å—Å—è –≤ –¥–æ–º–µ',
      '–ü–æ—á–∏–Ω–∏—Ç—å —á—Ç–æ-—Ç–æ –≤ –¥–æ–º–µ',
      '–°—å–µ–∑–¥–∏—Ç—å –≤ –æ—Ç–ø—É—Å–∫',
      '–û—Ç–ø—Ä–∞–≤–∏—Ç—å—Å—è –≤ –ø—É—Ç–µ—à–µ—Å—Ç–≤–∏–µ',
      '–í—ã–Ω–µ—Å—Ç–∏ –º—É—Å–æ—Ä',
      '–ö—É–ø–∏—Ç—å –≤–∫—É—Å–Ω—è—à–µ–∫',
    ],
    // placeholder: '',
  };

  onFormSubmit = event => {
    event.preventDefault();
    const todo = { id: uuidv4(), text: this.state.text, type: this.state.type, completed: false };
    todo.text !== '' && this.props.handleFormSubmit(todo);
    this.formReset();
  };

  formReset = () => this.setState({ text: '' });

  changeInputValue = event => this.setState({ text: event.target.value });

  getRandomInRange(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  render() {
    return (
      <form className="todo-creator__form" onSubmit={this.onFormSubmit}>
        <input
          type="text"
          value={this.state.text}
          onChange={this.changeInputValue}
          autoComplete="off"
          placeholder={this.state.placeholders[this.getRandomInRange(0, this.state.placeholders.length - 1)]}
        />
        <button type="submit">–î–æ–±–∞–≤–∏—Ç—å</button>
      </form>
    );
  }
}

export default TodoCreator;
