import React from 'react';

import './Stats.scss';

const Stats = ({ todos }) => {
  return (
    <>
      <div className="stats">
        <p className="stats__all-todos">–í—Å–µ–≥–æ: {todos.length}</p>
        <p className="stats__important-todos">
          –í–∞–∂–Ω—ã—Ö: {todos.filter(todo => todo.type === 'important' && !todo.completed).length}
        </p>
        <p className="stats__done-todos">–í—ã–ø–æ–ª–Ω–µ–Ω–Ω—ã—Ö: {todos.filter(todo => todo.completed).length}</p>
      </div>
      <div className="line"></div>
    </>
  );
};

export default Stats;
